<template>
  <div>
    <m-title type="1">Textarea</m-title>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-input v-model="text1" rows="5" type="textarea" placeholder="" @change="handlerChange"></m-input>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-input v-model="text2" rows="5" type="textarea" disabled placeholder=""></m-input>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-input v-model="text3" rows="5" type="textarea" readonly placeholder=""></m-input>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-input v-model="text4" rows="5" type="textarea" :limit="50" placeholder=""></m-input>
      </m-box>
    </m-panel>
  </div>
</template>

<script>
export default {
  data () {
    return {
      text1: '',
      text2: '',
      text3: '',
      text4: ''
    }
  },
  methods: {
    handlerChange (val) {
      this.$toast(val)
    }
  }
}
</script>
