<template>
  <div>
    <m-title type="1">Toast</m-title>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="primary" @click="showToast1">toast</m-button>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="minor" @click="showToast2">toast</m-button>
        <m-button type="minor" plain @click="showToast3">toast</m-button>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="primary" plain @click="showToast4">toast</m-button>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button @click="showToast5">toast</m-button>
      </m-box>
    </m-panel>
  </div>
</template>

<script>
export default {
  methods: {
    showToast1 () {
      this.$toast('')
    },
    showToast2 () {
      this.$toast.top('')
    },
    showToast3 () {
      this.$toast.center('')
    },
    showToast4 () {
      this.$toast({
        message: '',
        timeout: 3000,
        type: 'bottom'
      })
    },
    showToast5 () {
      this.$toast.center({
        icon: 'android-done',
        message: '',
        timeout: 2000
      })
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/ .mtui-btn:not(:last-child) {
  margin-bottom: 16px;
}
</style>
