<template>
  <div>
    <m-title type="1">Dialog</m-title>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="primary" @click="showDialog1">dialog</m-button>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="default" @click="showDialog2">dialog</m-button>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="minor" @click="showDialog3">dialog</m-button>
      </m-box>
    </m-panel>

    <m-dialog v-model="dialogShow1">
      
    </m-dialog>
    <m-dialog v-model="dialogShow2" :closeOnClickMask="false">
      <div slot="title"></div>
      <p></p>
      <m-button type="minor" text @click="close2" slot="foot"></m-button>
    </m-dialog>
    <m-dialog v-model="dialogShow3" showCloseButton>
      <div slot="title"></div>
      <p></p>
    </m-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      dialogShow1: false,
      dialogShow2: false,
      dialogShow3: false
    }
  },
  methods: {
    showDialog1 () {
      this.dialogShow1 = true
    },
    showDialog2 () {
      this.dialogShow2 = true
    },
    showDialog3 () {
      this.dialogShow3 = true
    },
    close2 () {
      this.dialogShow2 = false
    }
  }
}
</script>
