<template>
  <div>
    <m-title type="1">Alert</m-title>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="primary" @click="showAlert1">alert</m-button>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="default" @click="showAlert2">alert</m-button>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="minor" plain @click="showAlert3">alert</m-button>
      </m-box>
    </m-panel>
  </div>
</template>

<script>
export default {
  methods: {
    showAlert1 () {
      this.$alert('')
    },
    showAlert2 () {
      this.$alert({
        title: '',
        message: '',
        confirmTxt: ''
      })
    },
    showAlert3 () {
      this.$alert({
        title: '',
        message: '',
        confirmTxt: '',
        onConfirm () {
          this.$toast('')
        }
      })
    }
  }
}
</script>
