<template>
  <div>
    <m-title type="1">Confirm</m-title>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="primary" @click="showConfirm1">confirm</m-button>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="default" @click="showConfirm2">confirm</m-button>
      </m-box>
    </m-panel>
    <m-panel>
      <div slot="head"></div>
      <m-box>
        <m-button type="minor" plain @click="showConfirm3">confirm</m-button>
      </m-box>
    </m-panel>
  </div>
</template>

<script>
export default {
  methods: {
    showConfirm1 () {
      this.$confirm('')
    },
    showConfirm2 () {
      this.$confirm({
        title: '',
        message: '',
        confirmTxt: '',
        cancelTxt: ''
      })
    },
    showConfirm3 () {
      this.$confirm({
        title: '',
        message: '',
        confirmTxt: '',
        cancelTxt: '',
        onConfirm () {
          this.$toast('')
        },
        onCancel () {
          this.$toast('')
        }
      })
    }
  }
}
</script>
